.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: linear-gradient(135deg, #fbbf24 0%, #f59e0b 25%, #d97706 50%, #b45309 75%, #92400e 100%);
  background-size: 400% 400%;
  animation: dogeGradient 15s ease infinite;
  color: white;
  font-family: 'Comic Sans MS', 'Chalkduster', sans-serif;
  position: relative;
  overflow: hidden;
}

.App::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image:
    radial-gradient(circle at 20% 80%, rgba(255, 255, 255, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(59, 130, 246, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(251, 191, 36, 0.1) 0%, transparent 50%);
  pointer-events: none;
}

@keyframes dogeGradient {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

@keyframes dogeBounce {
  0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
  40% { transform: translateY(-10px); }
  60% { transform: translateY(-5px); }
}

@keyframes dogeWag {
  0%, 100% { transform: rotate(0deg); }
  25% { transform: rotate(5deg); }
  75% { transform: rotate(-5deg); }
}

@keyframes coinSpin {
  from { transform: rotateY(0deg); }
  to { transform: rotateY(360deg); }
}

.app-error {
  padding: 2rem;
  text-align: center;
  background: linear-gradient(135deg, #ef4444, #dc2626);
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  animation: dogeBounce 2s infinite;
}

.app-error code {
  background: rgba(0, 0, 0, 0.3);
  padding: 0.5rem;
  border-radius: 12px;
  font-family: 'Courier New', monospace;
  border: 2px solid rgba(255, 255, 255, 0.2);
}

.app-header {
  background: rgba(255, 255, 255, 0.15);
  padding: 1rem 0;
  backdrop-filter: blur(15px);
  border-bottom: 3px solid rgba(251, 191, 36, 0.5);
  box-shadow: 0 4px 20px rgba(251, 191, 36, 0.2);
}

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
}

.header-content h1 {
  margin: 0;
  font-size: 2.5rem;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  animation: dogeWag 3s ease-in-out infinite;
  background: linear-gradient(45deg, #ffffff, #fbbf24);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.disconnect-btn {
  background: linear-gradient(135deg, #ef4444, #dc2626);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
  padding: 0.75rem 1.5rem;
  border-radius: 25px;
  cursor: pointer;
  font-weight: bold;
  font-size: 1rem;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  box-shadow: 0 4px 15px rgba(239, 68, 68, 0.3);
}

.disconnect-btn:hover {
  background: linear-gradient(135deg, #dc2626, #b91c1c);
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 6px 20px rgba(239, 68, 68, 0.4);
}

.app-nav {
  background: rgba(255, 255, 255, 0.1);
  padding: 1rem 0;
  display: flex;
  justify-content: center;
  gap: 2rem;
  border-bottom: 2px solid rgba(251, 191, 36, 0.3);
}

.nav-btn {
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.8), rgba(37, 99, 235, 0.8));
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.4);
  padding: 0.75rem 1.5rem;
  border-radius: 30px;
  cursor: pointer;
  font-weight: bold;
  font-size: 1rem;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  position: relative;
  overflow: hidden;
}

.nav-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.nav-btn:hover::before {
  left: 100%;
}

.nav-btn:hover {
  background: linear-gradient(135deg, rgba(37, 99, 235, 0.9), rgba(29, 78, 216, 0.9));
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 6px 20px rgba(59, 130, 246, 0.4);
}

.nav-btn.active {
  background: linear-gradient(135deg, #fbbf24, #f59e0b);
  border-color: rgba(255, 255, 255, 0.8);
  color: #92400e;
  animation: coinSpin 2s linear infinite;
  box-shadow: 0 4px 15px rgba(251, 191, 36, 0.5);
}

.app-main {
  flex: 1;
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  box-sizing: border-box;
  position: relative;
  z-index: 1;
}

.app-footer {
  background: rgba(0, 0, 0, 0.3);
  padding: 1.5rem;
  text-align: center;
  font-size: 1rem;
  font-weight: bold;
  border-top: 3px solid rgba(251, 191, 36, 0.5);
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
  position: relative;
  z-index: 1;
}

.app-footer::before {
  content: '🐕';
  font-size: 1.5rem;
  margin-right: 0.5rem;
  animation: dogeBounce 3s ease-in-out infinite;
}

/* Component Styles */

.wallet-connect {
  max-width: 600px;
  margin: 0 auto;
  padding: 2.5rem;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0.05));
  border-radius: 25px;
  backdrop-filter: blur(20px);
  border: 3px solid rgba(251, 191, 36, 0.4);
  box-shadow: 0 8px 32px rgba(251, 191, 36, 0.2);
  position: relative;
  overflow: hidden;
  animation: dogeBounce 4s ease-in-out infinite;
}

.wallet-connect::before {
  content: '🐕';
  position: absolute;
  top: 10px;
  right: 20px;
  font-size: 2rem;
  opacity: 0.6;
  animation: dogeWag 2s ease-in-out infinite;
}

.wallet-connect h2 {
  margin-bottom: 1.5rem;
  font-size: 2.5rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  background: linear-gradient(45deg, #ffffff, #fbbf24);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: dogeWag 3s ease-in-out infinite;
}

.form-group {
  margin-bottom: 2rem;
  text-align: left;
}

.form-group label {
  display: block;
  margin-bottom: 0.75rem;
  font-weight: bold;
  font-size: 1.1rem;
  color: #ffffff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 1rem;
  border: 2px solid rgba(251, 191, 36, 0.3);
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  font-size: 1rem;
  font-weight: 500;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.form-group input::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

.form-group input:focus,
.form-group select:focus {
  border-color: #fbbf24;
  box-shadow: 0 0 20px rgba(251, 191, 36, 0.4);
  transform: scale(1.02);
}

.btn-primary {
  background: linear-gradient(135deg, #fbbf24, #f59e0b);
  color: #92400e;
  border: 2px solid rgba(255, 255, 255, 0.3);
  padding: 1.25rem 3rem;
  border-radius: 30px;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: bold;
  width: 100%;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  box-shadow: 0 6px 20px rgba(251, 191, 36, 0.3);
  position: relative;
  overflow: hidden;
}

.btn-primary::before {
  content: '🚀';
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1.2rem;
}

.btn-primary:hover {
  background: linear-gradient(135deg, #f59e0b, #d97706);
  transform: translateY(-3px) scale(1.02);
  box-shadow: 0 8px 25px rgba(251, 191, 36, 0.5);
}

.btn-primary:disabled {
  background: linear-gradient(135deg, #6b7280, #4b5563);
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.error-message {
  background: linear-gradient(135deg, #ef4444, #dc2626);
  color: white;
  padding: 1.5rem;
  border-radius: 15px;
  margin-bottom: 1.5rem;
  border: 2px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 4px 15px rgba(239, 68, 68, 0.3);
  animation: dogeBounce 1s ease-in-out;
}

.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
}

.spinner {
  width: 24px;
  height: 24px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-top: 3px solid #fbbf24;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.wallet-dashboard {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  max-width: 900px;
  margin: 0 auto;
}

.balance-card,
.address-card {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0.05));
  padding: 2.5rem;
  border-radius: 25px;
  backdrop-filter: blur(20px);
  border: 3px solid rgba(59, 130, 246, 0.4);
  box-shadow: 0 8px 32px rgba(59, 130, 246, 0.2);
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
  animation: dogeBounce 5s ease-in-out infinite;
}

.balance-card:hover,
.address-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px rgba(59, 130, 246, 0.3);
}

.balance-card::before {
  content: '💰';
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 2rem;
  opacity: 0.6;
  animation: coinSpin 3s linear infinite;
}

.address-card::before {
  content: '🏠';
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 2rem;
  opacity: 0.6;
  animation: dogeWag 2.5s ease-in-out infinite;
}

.balance-card h3,
.address-card h3 {
  margin-bottom: 1.5rem;
  font-size: 1.8rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  color: #ffffff;
}

.balance-amount {
  font-size: 3rem;
  font-weight: bold;
  margin: 1.5rem 0;
  color: #fbbf24;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  animation: coinSpin 4s linear infinite;
}

.address-display {
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  background: rgba(0, 0, 0, 0.3);
  padding: 1.25rem;
  border-radius: 12px;
  word-break: break-all;
  margin: 1.5rem 0;
  border: 2px solid rgba(251, 191, 36, 0.3);
  color: #ffffff;
}

.inscription-creator {
  max-width: 700px;
  margin: 0 auto;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0.05));
  padding: 3rem;
  border-radius: 25px;
  backdrop-filter: blur(20px);
  border: 3px solid rgba(251, 191, 36, 0.4);
  box-shadow: 0 8px 32px rgba(251, 191, 36, 0.2);
  position: relative;
  overflow: hidden;
  animation: dogeBounce 6s ease-in-out infinite;
}

.inscription-creator::before {
  content: '✍️';
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 2rem;
  opacity: 0.6;
  animation: dogeWag 2s ease-in-out infinite;
}

.inscription-creator h2 {
  margin-bottom: 2rem;
  font-size: 2.5rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  background: linear-gradient(45deg, #ffffff, #fbbf24);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: dogeWag 3.5s ease-in-out infinite;
}

.file-upload {
  border: 3px dashed rgba(251, 191, 36, 0.5);
  border-radius: 20px;
  padding: 3rem;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  background: rgba(255, 255, 255, 0.05);
  position: relative;
  overflow: hidden;
}

.file-upload::before {
  content: '📁';
  font-size: 3rem;
  display: block;
  margin-bottom: 1rem;
  animation: dogeBounce 2s ease-in-out infinite;
}

.file-upload:hover {
  border-color: #fbbf24;
  background: rgba(251, 191, 36, 0.1);
  transform: scale(1.02);
  box-shadow: 0 6px 20px rgba(251, 191, 36, 0.3);
}

.file-upload input[type="file"] {
  display: none;
}

.cost-estimate {
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.2), rgba(37, 99, 235, 0.2));
  padding: 2rem;
  border-radius: 20px;
  margin: 1.5rem 0;
  border: 2px solid rgba(59, 130, 246, 0.3);
  position: relative;
}

.cost-estimate::before {
  content: '💎';
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 1.5rem;
  opacity: 0.7;
  animation: coinSpin 2s linear infinite;
}

.cost-estimate h3 {
  margin-bottom: 1.5rem;
  font-size: 1.8rem;
  color: #ffffff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.cost-item {
  display: flex;
  justify-content: space-between;
  margin: 0.75rem 0;
  font-size: 1.1rem;
  font-weight: 500;
}

.cost-total {
  font-weight: bold;
  font-size: 1.3rem;
  border-top: 3px solid rgba(251, 191, 36, 0.5);
  padding-top: 1rem;
  margin-top: 1.5rem;
  color: #fbbf24;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  animation: coinSpin 3s linear infinite;
}

.inscription-history {
  max-width: 1100px;
  margin: 0 auto;
}

.history-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.inscription-card {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0.05));
  padding: 2rem;
  border-radius: 25px;
  backdrop-filter: blur(20px);
  border: 3px solid rgba(251, 191, 36, 0.4);
  box-shadow: 0 8px 32px rgba(251, 191, 36, 0.2);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  animation: dogeBounce 7s ease-in-out infinite;
}

.inscription-card::before {
  content: '🎨';
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 1.5rem;
  opacity: 0.6;
  animation: dogeWag 2s ease-in-out infinite;
}

.inscription-card:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 12px 40px rgba(251, 191, 36, 0.3);
  border-color: rgba(251, 191, 36, 0.6);
}

.inscription-preview {
  width: 100%;
  height: 220px;
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.2), rgba(37, 99, 235, 0.2));
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.5rem;
  overflow: hidden;
  border: 2px solid rgba(59, 130, 246, 0.3);
  position: relative;
}

.inscription-preview::before {
  content: '🐕';
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 1.5rem;
  opacity: 0.7;
  z-index: 2;
}

.inscription-preview img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  border-radius: 12px;
}

.inscription-id {
  font-family: 'Courier New', monospace;
  font-size: 0.85rem;
  opacity: 0.9;
  word-break: break-all;
  background: rgba(0, 0, 0, 0.3);
  padding: 0.75rem;
  border-radius: 10px;
  border: 1px solid rgba(251, 191, 36, 0.3);
  color: #ffffff;
}

@media (max-width: 768px) {
  .App {
    font-size: 14px;
  }

  .header-content h1 {
    font-size: 2rem;
  }

  .app-nav {
    flex-direction: column;
    gap: 1rem;
    padding: 1rem;
  }

  .header-content {
    flex-direction: column;
    gap: 1rem;
  }

  .wallet-dashboard {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .balance-card,
  .address-card {
    padding: 2rem;
  }

  .balance-amount {
    font-size: 2.5rem;
  }

  .history-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .inscription-card {
    padding: 1.5rem;
  }

  .inscription-preview {
    height: 180px;
  }

  .wallet-connect {
    padding: 2rem;
    margin: 1rem;
  }

  .inscription-creator {
    padding: 2rem;
    margin: 1rem;
  }

  .nav-btn {
    padding: 0.75rem 1rem;
    font-size: 0.9rem;
  }

  .btn-primary {
    padding: 1rem 2rem;
    font-size: 1rem;
  }

  .cost-estimate {
    padding: 1.5rem;
  }
}

/* Dogecoin-themed scrollbar */
::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background: rgba(251, 191, 36, 0.1);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #fbbf24, #f59e0b);
  border-radius: 10px;
  border: 2px solid rgba(255, 255, 255, 0.2);
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #f59e0b, #d97706);
}

/* Selection styling */
::selection {
  background: rgba(251, 191, 36, 0.3);
  color: #92400e;
}

::-moz-selection {
  background: rgba(251, 191, 36, 0.3);
  color: #92400e;
}

/* Wallet Onboarding Styles */
.wallet-onboarding {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 2rem;
  background: linear-gradient(135deg, #ff6b35 0%, #f7931a 50%, #2196f3 100%);
  animation: dogeWag 3s ease-in-out infinite;
}

.welcome-screen {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  padding: 3rem;
  text-align: center;
  max-width: 500px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  animation: dogeBounce 2s ease-in-out;
}

.welcome-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
  animation: coinSpin 2s linear infinite;
}

.welcome-screen h1 {
  color: #333;
  margin-bottom: 0.5rem;
  font-size: 2.5rem;
}

.welcome-subtitle {
  color: #666;
  margin-bottom: 2rem;
  font-size: 1.1rem;
}

.welcome-features {
  display: grid;
  gap: 1rem;
  margin-bottom: 2rem;
}

.feature {
  display: flex;
  align-items: center;
  gap: 1rem;
  background: rgba(255, 255, 255, 0.8);
  padding: 1rem;
  border-radius: 12px;
  font-weight: 500;
}

.feature-icon {
  font-size: 1.5rem;
}

.welcome-btn {
  background: linear-gradient(45deg, #ff6b35, #f7931a);
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 50px;
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(255, 107, 53, 0.4);
}

.welcome-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 107, 53, 0.6);
}

.welcome-note {
  color: #666;
  font-size: 0.9rem;
  margin-top: 1rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 8px;
}

.choice-screen {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  padding: 3rem;
  text-align: center;
  max-width: 600px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.choice-screen h2 {
  color: #333;
  margin-bottom: 1rem;
}

.choice-buttons {
  display: grid;
  gap: 2rem;
  margin: 2rem 0;
}

.choice-btn {
  background: rgba(255, 255, 255, 0.9);
  border: 2px solid transparent;
  border-radius: 16px;
  padding: 2rem;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: left;
}

.choice-btn:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
}

.create-btn:hover {
  border-color: #4CAF50;
  background: linear-gradient(135deg, rgba(76, 175, 80, 0.1), rgba(255, 255, 255, 0.9));
}

.import-btn:hover {
  border-color: #2196F3;
  background: linear-gradient(135deg, rgba(33, 150, 243, 0.1), rgba(255, 255, 255, 0.9));
}

.choice-icon {
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

.choice-title {
  font-size: 1.3rem;
  font-weight: bold;
  color: #333;
  margin-bottom: 0.5rem;
}

.choice-desc {
  color: #666;
  font-size: 0.9rem;
}

.backup-screen {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  padding: 3rem;
  max-width: 700px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.backup-screen h2 {
  color: #333;
  text-align: center;
  margin-bottom: 1rem;
}

.backup-warning {
  background: linear-gradient(45deg, #ff6b35, #f7931a);
  color: white;
  padding: 1rem;
  border-radius: 12px;
  text-align: center;
  font-weight: bold;
  margin-bottom: 2rem;
}

.seed-display {
  background: rgba(0, 0, 0, 0.05);
  border-radius: 12px;
  padding: 1.5rem;
  margin-bottom: 2rem;
}

.seed-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.toggle-seed-btn {
  background: #2196F3;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
}

.seed-phrase {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 0.5rem;
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  transition: filter 0.3s ease;
}

.seed-phrase.blurred {
  filter: blur(4px);
}

.seed-word {
  background: white;
  padding: 0.5rem;
  border-radius: 6px;
  text-align: center;
  border: 1px solid rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.25rem;
}

.word-number {
  color: #666;
  font-size: 0.8rem;
}

.backup-actions {
  text-align: center;
  margin-bottom: 2rem;
}

.copy-btn {
  background: #4CAF50;
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
}

.backup-tips {
  background: rgba(255, 235, 59, 0.1);
  border: 1px solid #ffeb3b;
  border-radius: 12px;
  padding: 1.5rem;
  margin-bottom: 2rem;
}

.backup-tips h3 {
  color: #f57c00;
  margin-bottom: 1rem;
}

.backup-tips ul {
  color: #333;
  text-align: left;
  margin: 0;
  padding-left: 1.5rem;
}

.backup-confirm {
  text-align: center;
  margin-bottom: 2rem;
}

.checkbox-label {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background: rgba(76, 175, 80, 0.1);
  padding: 1rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 500;
}

.checkbox-label input[type="checkbox"] {
  width: 18px;
  height: 18px;
}

.import-screen {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  padding: 3rem;
  max-width: 500px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.import-screen h2 {
  color: #333;
  text-align: center;
  margin-bottom: 1rem;
}

.seed-input {
  width: 100%;
  padding: 1rem;
  border: 2px solid rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  font-family: 'Courier New', monospace;
  font-size: 1rem;
  resize: vertical;
  background: white;
}

.seed-input:focus {
  outline: none;
  border-color: #2196F3;
}

.connecting-screen {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  padding: 3rem;
  text-align: center;
  max-width: 400px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.connecting-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
  animation: coinSpin 2s linear infinite;
}

.connecting-screen h2 {
  color: #333;
  margin-bottom: 1rem;
}

.back-btn {
  background: transparent;
  border: 1px solid rgba(0, 0, 0, 0.3);
  color: #666;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  margin-top: 1rem;
}

.back-btn:hover {
  background: rgba(0, 0, 0, 0.05);
}

/* Funding Guide Styles */
.funding-guide {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 2rem;
}

.funding-modal {
  background: white;
  border-radius: 20px;
  max-width: 600px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.funding-header {
  padding: 2rem;
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.funding-header h2 {
  color: #333;
  margin-bottom: 0.5rem;
}

.funding-header p {
  color: #666;
}

.funding-steps {
  padding: 2rem;
}

.step-indicator {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 2rem;
}

.step-dot {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  color: #666;
  transition: all 0.3s ease;
}

.step-dot.active {
  background: linear-gradient(45deg, #ff6b35, #f7931a);
  color: white;
}

.funding-step {
  text-align: center;
}

.step-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.funding-step h3 {
  color: #333;
  margin-bottom: 1rem;
}

.funding-step p {
  color: #666;
  margin-bottom: 1rem;
}

.address-display {
  background: rgba(0, 0, 0, 0.05);
  border-radius: 8px;
  padding: 1rem;
  margin: 1rem 0;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.address-display code {
  flex: 1;
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  word-break: break-all;
  color: #333;
}

.copy-address-btn {
  background: #2196F3;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
  white-space: nowrap;
}

.step-note {
  font-size: 0.9rem;
  color: #666;
  font-style: italic;
}

.funding-options {
  display: grid;
  gap: 1rem;
  margin: 2rem 0;
}

.option {
  display: flex;
  align-items: center;
  gap: 1rem;
  background: rgba(0, 0, 0, 0.05);
  padding: 1rem;
  border-radius: 8px;
}

.option-icon {
  font-size: 1.5rem;
}

.option strong {
  color: #333;
  display: block;
}

.option p {
  color: #666;
  margin: 0;
  font-size: 0.9rem;
}

.amount-guide {
  background: rgba(251, 191, 36, 0.1);
  border-radius: 8px;
  padding: 1rem;
  margin: 1rem 0;
}

.amount-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.amount-item:last-child {
  margin-bottom: 0;
}

.amount-label {
  color: #666;
}

.amount-value {
  font-weight: bold;
  color: #f7931a;
}

.warning-box {
  background: rgba(255, 107, 53, 0.1);
  border: 1px solid #ff6b35;
  border-radius: 8px;
  padding: 1rem;
  margin: 1rem 0;
  text-align: left;
}

.warning-box strong {
  color: #ff6b35;
}

.confirmation-steps {
  text-align: left;
  background: rgba(76, 175, 80, 0.1);
  border-radius: 8px;
  padding: 1rem;
  margin: 1rem 0;
}

.confirmation-steps li {
  margin-bottom: 0.5rem;
  color: #333;
}

.confirmation-steps li:last-child {
  margin-bottom: 0;
}

.funding-actions {
  display: flex;
  gap: 1rem;
  padding: 2rem;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  align-items: center;
}

.action-spacer {
  flex: 1;
}

.btn-secondary {
  background: rgba(0, 0, 0, 0.1);
  color: #666;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
}

.btn-secondary:hover {
  background: rgba(0, 0, 0, 0.2);
}

.btn-skip {
  background: transparent;
  color: #666;
  border: 1px solid rgba(0, 0, 0, 0.3);
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  cursor: pointer;
}

.btn-skip:hover {
  background: rgba(0, 0, 0, 0.05);
}

/* Quick Start Guide Styles */
.quick-start-guide {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 2rem;
}

.quick-start-modal {
  background: white;
  border-radius: 20px;
  max-width: 500px;
  width: 100%;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.quick-start-header {
  padding: 2rem;
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.quick-start-header h2 {
  color: #333;
  margin-bottom: 0.5rem;
}

.quick-start-header p {
  color: #666;
}

.tip-navigation {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  padding: 1rem;
}

.tip-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: all 0.3s ease;
}

.tip-dot.active {
  background: linear-gradient(45deg, #ff6b35, #f7931a);
  transform: scale(1.2);
}

.current-tip {
  padding: 2rem;
  text-align: center;
}

.tip-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.current-tip h3 {
  color: #333;
  margin-bottom: 1rem;
}

.current-tip p {
  color: #666;
  margin-bottom: 0;
}

.tip-progress {
  text-align: center;
  padding: 1rem;
  color: #666;
  font-size: 0.9rem;
}

.quick-start-actions {
  display: flex;
  gap: 1rem;
  padding: 2rem;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  align-items: center;
}
